
section{
    padding: 5rem;
    /* height: 100vh; */
}
.main-text{
    font-size: 40pt;
}
.btn{
    color:#ffffff;
    font-size: large;
    transition: 0.3 all;
    width: 200px;
}
.btn svg{
    fill:#ffffff;
    width: 24px;
    height: 24px;
}
.btn:hover svg{
    fill:var(--primary-color);
}
.navigate-btn{
    margin: 5rem auto;
}
.navigate-btn > a{
    margin: 0 auto;
}
