.cta-btns {
  gap: 30px;
}
.btn[disabled]{
    cursor: not-allowed !important;
    pointer-events: none;
}
.video-player-div > * {
  margin: 0 auto;
}
.video-player {
  height: 385px;
  margin: 1rem;
}
.recorder{
  /* height: 100vh; */
  padding-bottom: 1rem;
}
.recorder-mobile{
  display: none;
}
@media (orientation: portrait) {
  .recorder{
    display: none;
  }
  .recorder-mobile{
    /* height: 150vh; */
    display: block;
  }
}