header {
    background: var(--primary-color);
    height: 80px;
  }
  nav {
    display: flex;
  }
  header h2 {
    margin: 0;
    padding: 1rem;
    cursor: pointer;
  }
  header ul {
    list-style: none;
  }
  .nav-btn{
      width: 60px;
      margin-right: 20px;
  }
  @media (orientation: portrait) {
    header nav {
      flex-direction: column !important;
    }
    header{
      height: 150px;
    }
  }