.flex {
  display: flex;
}
.col {
  flex-direction: column;
}
.row {
  flex-direction: row;
}
.items-center {
  align-items: center;
}
.space-btw {
  justify-content: space-between;
}
.justify-center {
  justify-content: center;
}
.text-center {
  text-align: center;
}
.anim-btn {
  padding: 0.5rem 1rem;
  border: 2px solid white;
  cursor: pointer;
  font-weight: 800;
  position: relative;
  overflow: hidden;
  transition: all 0.35s;
  z-index: 1;
}
.anim-btn a{
    text-decoration: none;
    color: inherit;
}
.anim-btn:hover {
    color: var(--primary-color);
    border-color: var(--primary-color);
}
.anim-btn:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--primary-color);
  z-index: -2;
}
.anim-btn:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background-color: #ffffff;
  transition: all 0.3s;
  z-index: -1;
}
.anim-btn:hover::before {
  width: 100%;
}
a{
  text-decoration: none;
  color: #FFFFFF;
}
Header,Footer{
  position:fixed;
  width:100%;
  left:0;
  z-index: 9999;
}
Header{
  top:0;
}
Footer{
  bottom: 0;
}
.content{
  padding: 80px 0 40px 0;
}
@media (orientation: potrait) {
  .content{
    padding: 150px 0 80px 0;
  }  
}